  componentWillMount() {
    axios
      .post("/backendapi/pwd/list", {
        user: JSON.parse(localStorage.getItem("logInUserData"))._id,
      })
      .then((res) => {
        this.setState({ saved: res.data });
      });
  }

  toggleModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
      confirmPwd: "",
      showIdx: "",
    }));
  };

  toggleModal2 = () => {
    let passwordShowTime = JSON.parse(localStorage.getItem("logInUserData")).pwdtime;
    if(passwordShowTime > 10 || passwordShowTime < 2) {
      passwordShowTime=3;
    }

    if (this.state.confirmPwd === "123") {
      console.log("this.state.showIdx", this.state.showIdx);
      this.togglepwdshow(true, this.state.showIdx);
      setTimeout(
        function () {
          this.togglepwdshow(false, this.state.showIdx);
          this.setState({showPass: false});
        }.bind(this),
        passwordShowTime ? (passwordShowTime*1000) : 3000 
      );
      this.setState((prevState) => ({
        modal: !prevState.modal,
        confirmPwd: "",
      }));
    } else {
      toast.warning("Wrong Password");
    }
  };

  clearSaved = (e) => {
    this.state.selectAssetRef.current.select.clearValue();
    this.setState({
      pwd: "",
      ptype: "",
      userName: "",
      accountNumber: "",
      description: "",
    });
  }
  addToSaved = (e) => {
    this.setState({isLoading: true});
    e.preventDefault();
    const {
      saved,
      ptype,
      
      pwd,
      userName,
      accountNumber,
      description,
    } = this.state;

    if(
      ptype === '' || pwd === '' || accountNumber === ''
    ) {
      toast.warning('Mandatory Field Missing !!')
      this.setState({isLoading: false});
    } else {

    
    const newp = {
      pwdname: accountNumber,
      pwd,
      showPass: false,
      userName: userName,
      accountNumber: accountNumber,
      description: description,
    };
    const newp1 = {
      pwdname: accountNumber,
      pwd,
      ptype,
      userName: userName,
      user: JSON.parse(localStorage.getItem("logInUserData"))._id,
    };
    
    saved.push(newp);
    this.state.selectAssetRef.current.select.clearValue();
    this.setState({
      saved,
      pwd: "",
      ptype: "",
      userName: "",
      accountNumber: "",
      description: "",
    });
    axios.post("/backendapi/savepwd", newp1).then((res) => {
      //this.setState({saved:res.data});
      this.setState({isLoading: false});
      toast.success("Password saved successfully");
    });
  }
  };

  togglepwdshow = (b, i) => {
    const { saved , showPass} = this.state;
    
  
    console.log(b, i);
    saved[i].showPass = b;

    this.setState({ saved, showPass: b }); 
  };

  deletepwd = (i) => {
    let { saved } = this.state;
    
    axios.post("/backendapi/pwd/delete", saved[i]).then((res) => {
      //this.setState({saved:res.data});
     
      toast.warning("Password deleted successfully");
      saved = saved.filter(ex => {return ex._id !== saved[i]._id})
      this.setState({ saved });
    });
   
  };

